html,body{
  overflow-x: hidden;
  position: relative;
  width:100%;
  background-color:#fffae9;
}
.navbar{
  transition:all .3s ease;
}
.navbar-dark{
  &--modified{
    .navbar-nav{
      margin-right:13vw;
      .nav-item{
        padding: {
          top:20px;
          bottom:20px;
        };
      }
      .nav-link{
        color:white;
        font-size:1.5em;
        margin-right:7vmin;
      }
      .active {
        .nav-link{
          margin-right:16vmin;
          font-weight: bold;
        }
      }
    }
  }
}
.container-fluid{
  &--mainbg{
    background:url('../img/mainbackground.png');
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    height: 120vh;
    z-index:2;
    position: relative;
  }
  &--gtku{
    margin-top:100px;
    background:url('../img/threebg.png');
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    height:120vh;
    position:relative;
  }
}
.container{
  &--aboutus{
    margin-top:-162px;
    padding-top:1px;
  }
  &--wycf{
    margin-top:-230px;
    position:relative;
    z-index:3;
  }
  &--footer{
    margin-top:400px;
    padding-bottom:100px;
  }
  &--footer{
    color:$red;
    position:relative;
    a{
      color: $red;
      display:flex;
      align-items: flex-end !important;
      font-size:1.2em;
      white-space: nowrap;
      margin-left:10px;
    }
    .btn-red{
      display:flex;
      align-items:center !important;
      justify-content: center!important;
      width:40px;
      height:40px;
      color:white;
      background-color:$red;
      border-radius: 100%;
      font-size:1.4em;
      &:hover{
        color:white;
        background-color: $redHover;
      }
    }
    &__title{
      font-family: 'HouseSlant';
    }
  }
}
.header-body{
  position:absolute;
  top: 35%;
  left: 47%;
  color:$red;
  @media(max-width:768px){
    text-shadow: 0px 0px 10px white;
    left:50%;
    top:50%;
    transform:translate(-50%,-50%);
  }
  // transform: translate(0%,-50%);
  &__title{
    font-family: 'HouseSlant';
    font-size: 5em;
    line-height: 1;
    @media(max-width: 1218px){
      font-size:6.2vw; 
    }
    @media(max-width:768px){
      font-size:10vmin;
    }
  }
  &__link{
    color:$red;
    font-weight: bold;
    letter-spacing: 1.2px;
    padding: {
      left:40px;
      right:40px;
      top:7.5px;
      bottom:7.5px;
    }
    background-color:white;
    border-radius:12px;
    &:hover{
      text-decoration: none;
      background-color:#f0f0f0;
      color:$red;
    }
  }
}
.menu-mobile{
  position:fixed;
  z-index:200;
  right:0;
  transition:all .3s ease;
  .hamburger{
    &--position{
      position:absolute;
      z-index:2;
      top:20px;
      right:100px;
      outline:none;
      transition: all .3s ease;
      &.is-active{
        right:200px;
      }
      @media(max-width: 568px){
        right:4vw;
        &.is-active{
          right:4vw;
        }
      }
    }
  }
  .menu-mobile__body{
    position:absolute;
    width:0; 
    top:0;
    right:0;
    z-index:1;
    background-color: $yellow;
    height:100vh;
    box-shadow:0px 0px 10px rgba(black,.4);
    transition:all .3s ease;
    &.active{
      width:300px;
      @media(max-width: 568px){
        width:100vw;
      }
    }
  }
  .menu-body{
    margin-top:16vh;
    margin-left:5vw;
    transition:all .3s ease;
    overflow-y: scroll;
    @media(max-width: 568px){
      text-align:center;
    }
    &-item{
      font-size:22px;
      padding: {
        top:10px;
        bottom:10px;
      }
      &__link{
        color:white;
        text-decoration: none;
      }
      @media(max-width: 568px){
        font-size:30px;
        padding: {
          top:20px;
          bottom:20px;
        }
      }
      &.active{
        font-weight: bold;
      }
    }
  }
}
.content-page{
  &--aboutus{
    margin: {
      top:400px;
      bottom:150px;
    }
  }
  &--wycf{
    margin:100px;
    position: relative;
    z-index:4;
  }
  &--WES{
    padding: {
      left:75px;
      right:75px;
      top:75px;
      bottom:75px;
    }
  }
  &--gtku{
    margin-top:200px;
    margin-bottom:200px;
  }
  color:$red;
  &__title{
    font-family: 'HouseSlant';
  }
  &__link{
    color:white;
    font-weight: bold;
    letter-spacing: 1.2px;
    padding: {
      left:40px;
      right:40px;
      top:7.5px;
      bottom:7.5px;
    }
    background-color:$red;
    border-radius:12px;
    &:hover{
      text-decoration: none;
      background-color:$redHover;
      color:white;
    }
  }
}
.bgborder{
  &1{
    position: absolute;
    left:0;
    width: 0;
    height: 1400px;
    border-top: 300px solid transparent;
    border-left: 100vw solid $blue;
    border-bottom: 300px solid transparent;
  }
  &2{
    position: absolute;
    left:0;
    width: 0;
    height: 645px;
    margin-top:600px;
    border-top: 300px solid transparent;
    border-right: 100vw solid #fffae9;
    border-bottom: 300px solid transparent;
  }
}
.theme{
  position: relative;
  z-index:2;
  &--modified{
    margin-top:100px;
    .content{
      padding-top:340px !important;
      padding:50px;
      @media(max-width:768px){
        padding-top:280px !important;
      }
    }
    .bgborder1{
      height:1350px;
    }
  }
  
}
.slick-theme{
  .slick-list{
    overflow: visible;
    @media(max-width:768px){
      overflow: hidden;
    }
  }
  .slick-arrow{
    position:absolute;
    top:50%;
    color:white;
    transform:translateY(-50%);
    z-index:5;
    font-size:32px;
    cursor:pointer;
  }
  .slick-custom-next {
    right: -30px;
  }
  .slick-custom-prev {
    left: -30px;
  }
  &--wycf{
    .slick-custom-next {
      right: -30px;
      color:$blue;
    }
    .slick-custom-prev {
      color:$blue;
      left: -30px;
    }
  }
  @media(max-width:576px){
    .slick-custom-next {
      right: -20px;
    }
    .slick-custom-prev {
      left: -20px;
    }
  }
}
.content{
  padding-top:400px !important;
  padding:50px;
  .item-theme{
    transition: all .3s ease;
    padding:10px;
    color:$red;
    &__body{
      padding:20px;
      padding-top:40px;
      h1{
        font-family: 'HouseSlant';
      }
    }
    @media(min-width:768px){
      &:hover{
        transform: translateY(-10%);
        cursor: pointer;
      }
    }
  }
}
.image{
  position: relative;
  width:100%;
  &:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }
  &__content {
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
  }
}
.col{
